#recent-work {
    color: #fff;
    font-family: 'Roboto Slab', serif;
    display: flex;
    align-items: center;
    .slick-track {
        display: flex;
    }
    .slick-slide {
        height: auto;
        justify-content: center;
    }
    .slick-dots li {
        button {
            &::before {
                color: #DCDCDC;
            }
        }
        &.slick-active {
            button {
                &::before {
                    color: #fff;
                }
            }
        }
    }
    .slick-next, .slick-prev {
        width: auto;
        &::before {
            content: "";
        }
    }
    .fa {
        color: #fff;
        width: auto;
        padding: 0;
        margin: auto;
    }
}

.recent-work-wrapper {
    padding: 0;
}

.recent-work-item {
    background-color: rgba(255, 255, 255, 0.3);
    padding: 30px 30px;
    margin: 0 7.5px;
    text-align: center;
    p {
        text-align: left;
    }
}

.recent-work-content {
    h1 {
        margin-top: 0;
        margin-bottom: 2em;
    }
}

@media (min-width: $large) {
    #recent-work {
        .slick-dots li {
            button {
                &::before {
                    font-size: 30px;
                }
            }
        }
    }

    .recent-work-wrapper {
        align-items: center;
    }

    .recent-work-content {
        width: 1000px;
    }
}